


















































import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";

export interface ISpeedDialItem {
  to?: string;
  click?: () => void;
  icon: string;
  name: string;
}

@Component({})
export default class SpeedDial extends Mixins() {
  fab = false;

  @Prop({
    type: Array,
    default: () => []
  })
  items!: ISpeedDialItem[];

  loading: boolean[] = [];

  @Watch("items", { deep: true, immediate: true })
  watchItems(cur: ISpeedDialItem[], prev?: ISpeedDialItem[]) {
    if (!prev || prev.length !== cur.length) {
      this.loading = new Array(cur.length).fill(false);
    }
  }

  async onItemClick(item: ISpeedDialItem, idx: number) {
    if (item.click) {
      Vue.set(this.loading, idx, true);
      try {
        await item.click();
      } catch (err) {
        Vue.set(this.loading, idx, false);
        throw err;
      }
      Vue.set(this.loading, idx, false);
    }
  }
}
